/* Общие стили */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f8f8f8;
  color: #333;
}

/* Стили для навигации */
nav {
  background-color: #333;
  color: white;
  padding: 10px;
  text-align: center;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

nav ul li a:hover {
  text-decoration: underline;
}

/* Стили для главной части */
main {
  padding: 20px;
  text-align: center;
}

h1 {
  font-size: 2.5em;
  margin-bottom: 10px;
}

p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

/* Стили для галереи */
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.photo-card {
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 15px;
  max-width: 260px;
  overflow: hidden;
  text-align: left;
  transition: transform 0.3s;
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card h3 {
  font-size: 1.5em;
  margin: 10px;
}

.photo-card a {
  display: block;
  padding: 10px;
  text-align: center;
  background-color: #333;
  color: white;
  text-decoration: none;
  font-weight: bold;
}

.photo-card a:hover {
  background-color: #555;
}

/* Стили для подвала */
footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 0px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}

/* Стили для формы */
form {
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
}

form div {
  margin-bottom: 15px;
}

form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

form input, form textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

form button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1em;
  border-radius: 5px;
}

form button:hover {
  background-color: #555;
}
